/**
 * Breakpoint Configuration
 * @see https://github.com/Team-Sass/breakpoint/wiki
 *
 */

/////////////////////
// Global Breakpoints

// Avoid using in favor of atomic, content-specific, breakpoints.
// These should be used for generic code, like layouts and typography, only.

$xs: 320px;
$small: 480px;
$medium: 720px;
$large: 920px;
$xl: 1080px;
$xl-zoom: 1140px;
$desktop: 1400px;
$xxl: 1920px;


// The max-width breakpoint is used when the design should be applied at whatever the
// largest breakpoint is regardless of actual pixel value. e.g. removing outer margin on body wrapper
$max-width: 1438px;

$grid-large: 1000px;
$grid-xl: 1300px;

$content-max-width: 1438px;
$grid-breakpoints: 12;

/// Mixin - xs Breakpoint
/// Allows easier @include xs {} syntax
@mixin xs {
  @include breakpoint($xs) {
    @content;
  }
}

/// Mixin - small Breakpoint
/// Allows easier @include small {} syntax
@mixin small {
  @include breakpoint($small) {
    @content;
  }
}

/// Mixin - medium Breakpoint
/// Allows easier @include medium {} syntax
@mixin medium {
  @include breakpoint($medium) {
    @content;
  }
}

/// Mixin - large Breakpoint
/// Allows easier @include large {} syntax
@mixin large {
  @include breakpoint($large) {
    @content;
  }
}

/// Mixin - xl Breakpoint
/// Allows easier @include xl {} syntax
@mixin xl {
  @include breakpoint($xl) {
    @content;
  }
}

/// Mixin - xl-zoom Breakpoint
/// Allows easier @include xl-zoom {} syntax
@mixin xl-zoom {
  @include breakpoint($xl-zoom) {
    @content;
  }
}

/// Mixin - desktop Breakpoint
/// Allows easier @include desktop {} syntax
@mixin desktop {
  @include breakpoint($desktop) {
    @content;
  }
}

/// Mixin - xxl Breakpoint
/// Allows easier @include xxl {} syntax
@mixin xxl {
  @include breakpoint($xxl) {
    @content;
  }
}

/// Mixin - max-width Breakpoint
/// Allows easier @include max-width {} syntax
@mixin max-width {
  @include breakpoint($max-width) {
    @content;
  }
}

@mixin grid-large {
  @include breakpoint($grid-large) {
    @content;
  }
}


@mixin grid-xl {
  @include breakpoint($grid-xl) {
    @content;
  }
}
